


















import { Component, Prop, Vue } from 'vue-property-decorator';
import { Player } from './models/Player';
import { PlayerState } from './models/Constants';
import { AppModule } from '@/store/modules/app';
import { EventBus } from '@/main';
import { PlayerCommand } from '@/models/Constants';

@Component({
  name: 'MainForm',
  components: {},
})
export default class MainForm extends Vue {
  /*Youtube*/
  @Prop() videoId!: string;
  youtubeCallbackName = 'onYouTubeIframeAPIReady';
  youtubeExistsFlag = '$isYoutubeFrameAPIReady';
  hasYTFrame: boolean;
  player1: Player = null;
  /*v-zoomer*/
  zoomed = false;

  init() {
    if (!this.hasYoutubeFrameAPI()) {
      this.injectYoutubeFrameAPI();
    }
    this.whenYoutubeAPIReady().then(
      () => {
        const playerElement = this.$el.querySelector('.video-placeholder');
        playerElement.id = 'player-'
          + Math.floor(Math.random() * 1024)
          + Date.now()
          + Math.floor(Math.random() * 1024);

        //@ts-ignore
        this.player1 = new Player(YT, 1, playerElement, this.youtubeEventsHandler,
          {
            videoId: this.youtubeVideoID,
            autoPlay: false,
          },
        );
      },
      error => console.error(error),
    );

  }

  mounted() {
    this.init();
  }

  created() {
    EventBus.$on('PlayerCommand', command => {
      this.onCommand(command);
    });
  }

  onCommand(payload) {
    const { command } = payload;
    switch (command) {
      case PlayerCommand.play:
        this.player1.play();
        break;
      case PlayerCommand.pause:
        this.player1.stop();
        break;
      case PlayerCommand.zoomIn:
        this.zoomIn();
        break;
      case PlayerCommand.zoomOut:
        this.zoomOut();
        break;
      case PlayerCommand.down:
        this.setOffset(0,-300)
        break;
      case PlayerCommand.up:
        this.setOffset(0,0)
        break;
    }
  }

  /*Youtube*/
  get youtubeVideoID() {
    if (this.videoId.indexOf(':/') !== -1) {
      const catcher = /(?:youtube\.com\/(?:[^\/]+\/.+\/|(?:v|e(?:mbed)?)\/|.*[?&]v=)|youtu\.be\/)([^"&?\/ ]{11})/i;
      const res = catcher.exec(this.videoId);
      if (res && res[1]) {
        return res[1];
      }
    }
    return this.videoId;
  }

  whenYoutubeAPIReady() {
    const existsFlag = this.youtubeExistsFlag;
    return new Promise(function(resolve, reject) {
      let elapsed = 0;
      let intervalHandle;
      let checker = function() {
        elapsed += 48;
        if (!!window[existsFlag]) {
          clearTimeout(intervalHandle);
          resolve();
        } else {
          if (elapsed <= 15000) {
            intervalHandle = setTimeout(checker, 48);
          } else {
            reject('timeout');
          }
        }
      };
      setTimeout(checker, 48);
    });
  }

  hasYoutubeFrameAPI() {
    if (!this.hasYTFrame) {
      this.hasYTFrame = !!(document.getElementsByClassName('.yt-frame-api').length);
    }
    return this.hasYTFrame;
  }

  injectYoutubeFrameAPI() {
    const youtubeExistsFlag = this.youtubeExistsFlag;
    const youtubeCallbackName = this.youtubeCallbackName;

    window[this.youtubeCallbackName] = window[this.youtubeCallbackName] || function() {
      window[youtubeExistsFlag] = true;
      window[youtubeCallbackName] = null;
      delete window[youtubeCallbackName];
    };

    let tag = document.createElement('script');
    let first = document.getElementsByTagName('script')[0];
    tag.src = 'https://www.youtube.com/iframe_api';
    tag.className = 'yt-frame-api';
    first.parentNode.insertBefore(tag, first);
  }

  youtubeEventsHandler(event) {
    const { state, id, info } = event;
    AppModule.SET_PLAYER_IS_PLAYING(state);
    console.log(id, Object.keys(PlayerState).find(key => PlayerState[key] === state));

    // this.logYoutubeState(id, state, info);
    //
    // let video
    // switch (state) {
    //   case PlayerState.READY:
    //     this.logState(id, state, info)
    //     if (id == 1) {
    //       video = this.videoList.next();
    //       if (video.videoId === this.activePlayer.currentVideoId) {
    //         this.activePlayer.seek(video)
    //       } else {
    //         console.log(57624588857567)
    //       }
    //     }
    //     break
    //   case PlayerState.ENDED:
    //     this.logState(id, state, info)
    //     video = this.videoList.next();
    //     if (video.videoId === this.activePlayer.currentVideoId) {
    //       this.activePlayer.seek(video)
    //     } else {
    //       this.changePlayerActivity()
    //       // this.passivePlayer.stop()
    //       this.activePlayer.seek(video)
    //     }
    //     break
    //   default:
    // }
  }

  /*v-zoomer*/
  zoomIn() {
    //@ts-ignore
    this.$refs.zoomer.zoomIn();
  }

  zoomOut() {
    //@ts-ignore
    this.$refs.zoomer.zoomOut();
  }

  setOffset(x = 0, y = 0) {
    //@ts-ignore
    this.$refs.zoomer.setOffset(x, y);
  }


  /*  <div>
      zoomed: {{ zoomed }}
    <v-btn @click="$refs.player_form.zoomIn()">+</v-btn>
      <v-btn @click="$refs.player_form.zoomOut()">-</v-btn>
      <v-btn @click="$refs.player_form.setOffset(0,-300)"></v-btn>
      <v-btn @click="$refs.player_form.setOffset(0,0)"></v-btn>
      </div>*/

}
