

























import { Component, Vue, Prop } from 'vue-property-decorator';
import { RouteConfig } from 'vue-router';
import { routerOptions } from '@/router';
import {AppModule} from  '@/store/modules/app'

@Component({
  name: 'Toolbar',
})
export default class Toolbar extends Vue {
  @Prop({ required: true }) public drawer!: boolean;

  get routes(): RouteConfig[] | undefined {
    return routerOptions.routes!.filter((route) => {
      return route.meta.nav;
    });
  }

  get drawerState(){
    return AppModule.drawerState
  }

  set drawerState(val){
    AppModule.SET_DRAWER_STATE(val)
  }

}
