




















































import { Component, Vue } from 'vue-property-decorator';
import  { AppModule} from '@/store/modules/app'
import { EventBus } from '@/main'
import {PlayerCommand} from '@/models/Constants'

@Component
export default class ComponentA extends Vue {

  get playerIsPlaying(){
    return AppModule.playerIsPlaying
  }
  onArrow(number: number) {
    switch (number) {
      case 2:
        EventBus.$emit('PlayerCommand', {command:PlayerCommand.down})
        break
      case 8:
        EventBus.$emit('PlayerCommand', {command:PlayerCommand.up})
        break

    }

  }

  onPlayPause(){
    if(this.playerIsPlaying){
      EventBus.$emit('PlayerCommand', {command:PlayerCommand.pause})
    }else {
      EventBus.$emit('PlayerCommand',{command:PlayerCommand.play})
    }
  }
  zoomIn() {
    EventBus.$emit('PlayerCommand', {command:PlayerCommand.zoomIn})
  }

  zoomOut() {
    EventBus.$emit('PlayerCommand', {command:PlayerCommand.zoomOut})
  }
}
