import { VuexModule, Module, Mutation, Action, getModule } from 'vuex-module-decorators';
import store from '@/store';
import { PlayerState } from '@/models/Constants';

export interface IAppState {
}


const name = "app";
if (store.state[name]) {
  store.unregisterModule(name);
}
@Module({ dynamic: true, store, name })
class App extends VuexModule implements IAppState {
  private _drawerState = false;
  private _playerIsPlaying = false;

  get drawerState() {
    // return false
    return this._drawerState;
  }

  get playerIsPlaying() {
    return this._playerIsPlaying;
  }

  @Mutation
  SET_DRAWER_STATE(val: boolean) {
    this._drawerState = val;
  }

  @Mutation
  SET_PLAYER_IS_PLAYING(val) {

    this._playerIsPlaying = (val === PlayerState.PLAYING  || val === PlayerState.BUFFERING);
    // if(val == PlayerState.PLAYING){
    //   this._playerIsPlaying = true;
    // } else {
    //   this._playerIsPlaying = false;
    // }
  }
}

export const AppModule = getModule(App);
