



























import { Component, Vue, Prop } from "vue-property-decorator";
interface ICalculatorInput {
  current: string;
  operator: string;
  prev: string;
}
@Component
export default class Calculator extends Vue {
  total = 0
  key(num) {
    return this.total += num;
  }
  clear() {
    return this.total = 0;
  }
  equal() {
    let equal = this.total;
    //@ts-ignore
    return this.total = eval(equal);
  }
}
