






































import { Component, Vue } from 'vue-property-decorator';
import ComponentA from '@/components/ComponentA.vue';
import ComponentB from '@/components/ComponentB.vue';
import Calculator from '@/components/Calculator.vue';


@Component({
  components: {
    ComponentA,
    ComponentB,
    Calculator,
  },
})
export default class TransparentPanel extends Vue {
  width = 200;
  height = 100;
  x = 100;
  y = 100;
  /*Tabs*/
  tab = null;
  tabItems = [
    { tab: 'Component A', content: 'ComponentA', icon: 'videocam' },
    // { tab: 'Component A', content: 'ComponentA', icon: 'settings_overscan' },
    { tab: 'Calculator', content: 'Calculator', icon: 'home' },
    { tab: 'Component B', content: 'ComponentB', icon: 'gavel' },
  ];

  mounted() {
    const tmp = localStorage.getItem('TradingPanelSettings');
    if (tmp) {
      try {
        const tempObj = JSON.parse(tmp);

        this.width = tempObj.hasOwnProperty('width') ? tempObj.width : 100;
        this.height = tempObj.hasOwnProperty('height') ? tempObj.height : 50;
        this.y = tempObj.hasOwnProperty('y') ? tempObj.y : 20;
        this.x = tempObj.hasOwnProperty('x') ? tempObj.x : 20;
      } catch (e) {
        console.error(e.message);
      }
    }
  }

  /*vue-draggable-resizable*/
  saveXY() {
    const panelSettings = {
      x: this.x,
      y: this.y,
      width: this.width,
      height: this.height,
    };
    localStorage.setItem('TradingPanelSettings', JSON.stringify(panelSettings));
  }

  onResize(x, y, width, height) {
    this.x = x;
    this.y = y;
    this.width = width;
    this.height = height;
    this.saveXY();
  }

  onDrag(x, y) {
    this.x = x;
    this.y = y;
    this.saveXY();
  }
}
